
/*Обнуление*/
*{
	padding: 0;
	margin: 0;
	border: 0;
}
*,*:before,*:after{
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}

nav,footer,header,aside{display: block;}

html,body, #root{
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,button,textarea{font-family:inherit;}

input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight: 400;}
/*--------------------*/
* {
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  
}
.img {
	z-index: -1;
	position: absolute;
	width: 100%;
	height: 100%;
	object-fit: cover;
	opacity: 0.77;
}
.background {
	z-index: -2;
	position: absolute;
	width: 100%;
	height: 100%;
	object-fit: cover;
	background-color: #131929;
}

.header {
  height: 100px;
  font-weight:  bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 42px;
  border-bottom: 1px solid black;
}
.logout-btn {
	width: 50px;
	height: 50px;
}
.login-btn {
	width: 40px;
	height: 40px;
	border-radius: 30px;
	margin-right: 5px;
}


.auth {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-right: 5%;

}
.text {
	position: relative;
	color: white;
	font: 18px "Roboto";
	cursor: pointer;
	line-height: 1; 
	text-decoration: none; 
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.text:after{
	display: block;
	position: absolute;
	left: 0; 
	width: 0;
	height: 2px;
	background-color: #fa721e; 
	content: "";
	transition: width 0.3s ease-out; 
}

.text:hover:after,
.text:focus:after {
	width: 100%;
}
.menu {
	display: flex;
	margin-right: 20%;
	width: 30%;
	justify-content: space-between;
}
.footer {
	position: absolute;
	max-width: 100%;
	object-fit: cover;
}
.footer-image {
	width: 100%;
	object-fit: cover;
}
.modal {
	position: absolute;
	width: 627px;
	height: 375px;
	left: 50%;
  	top: 50%;
	transform: translate(-50%,-50%);
	
}
.modal-background {
	position: absolute;
	background-color: #0B2740;
	opacity: 0.7;
	width: 100%;
	height: 100%;
	border: 5px solid #1B3B6F;
	transform: matrix(1, 0, 0, -1, 0, 0);
	border-radius: 45px;
	z-index: -1;
	
}
.modal-content {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	height: 100%;
	position: absolute;
	
}
.modal-text {
	width: 569px;
	font: bold 24px "Roboto";
	text-align: center;
	color: white;
	line-height: 28px;
	font-weight: 900;
}
.trade-btn {
	width: 264px;
	height: 63px;
	text-align: center;
	color: white;
	font-weight: 900;
	font: bold 24px "Roboto";
	border: 1px solid #000000;
	box-sizing: border-box;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	border-radius: 20px;
	background-color:#142C52;
	
}
.sell-page{
	width: 100%;
	height: calc(100% - 100px);
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	
}

.modal-items{
	margin-top: 10px;
	width: 40%;
	height: 80%;
	background: #2A2833;
	border: 1px solid #000000;
	box-sizing: border-box;
	border-radius: 35px;
}


.background-steam-icon{
	width: 60px;
	height: 55px;
	background:  no-repeat url('https://cdn-icons.flaticon.com/png/512/2626/premium/2626307.png?token=exp=1638576863~hmac=4e16c8155bd8a16b91b65ba1d19fd30e');
	background-size: contain;
	
}
.modal-header{
	width: 100%;
	padding: 10px;
	display: flex;
	align-items: center;
	color: white;
	font-size: 20px;
	border-bottom: solid 1px #000000;
}

.link {
	padding: 10px;
	width: 100%;
	font-size: 20px;
	color:white;
	font-weight: bold;
	line-height: 25px;
}
.offer-link{
	width: 80%;
	border-radius: 35px;
	
}

.input-link{
	width:80%;
	height: 36px;
	margin-left: 10px;
    padding:10px;
	background-color: rgb(172, 172, 172);
  	border-radius: 5px;
	font-size: 18px;
	letter-spacing: normal;
	text-rendering: auto;
	font: 18px "Roboto";
}
.input-link:focus{
	box-shadow: 0px 0px 3px #fa721e;
	background: white;
}

.steam-icon{
	height: 40px;
	width: 40px;
	margin-right: 10px;
	padding-left: 10px;
}

.input-your-link{
	margin-left: 10px;
	font: 18px "Roboto";
}

.nickname{
	color: white;
	font: 18px "Roboto";
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
	margin-right: 30px;
}

.text-field{
	width:80%;
}

.text-with-icon{
	font-size: 28px;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.save-offer-link{
	display: inline;
	width: 16%;
	height: 36px;
	border-radius: 5px;
	margin-left: 10px;
	font: bold 16px "Roboto";
	background-color: #fa721e;
	color:white;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.selected {
	color: white;
	padding: 18px;
	margin-top: 1px;
	font-weight: 700;
}

.items{
	height: 73%;
	width: 95%;
	margin:auto;
	overflow-y: scroll;
	background: #22202b;
	border-radius: 5px;
	display: flex;
	flex-wrap: wrap;
}

.item{
	width: 25%;
	height: 30%;
	padding: 10px;
	border: 1px solid #00ffdd;
	display: flex;
	flex-direction: column;

}
.item:hover{
	background-color: #0B2740;
	cursor: pointer;
}
.icon {
	width: 100%;
	height: 70%;
}

.item-name{
	color: white;
	
}

.reload-inventory {
	background: #fa721e;
	color: white;
	border-radius: 5px;
	float: right;
	width: 70px;
	height: 15px;
}